.order-box {
  background-color: #1f1d2b;
  width: 100%;
  height: 100vh;
  padding: 24px;
  box-sizing: border-box;

}

.order-info {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: #FFFFFF;
}

.order-btn {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 7px 12px;
  background: #1F1D2B;
  border: 1px solid #393C49;
  box-sizing: border-box;
  border-radius: 8px;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  color: #EA7C69;
  outline: none;
  margin: 25px 0 20px 10px;
}

.order-btns {
  display: flex;
  align-items: center;
}

.order-items {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #FFFFFF;
}

.row-order-left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
}

.active-btn {
  background: #EA7C69;
  color: #fff;
}

.total-box {
  width: 100%;
  height: 150px;
  position: relative;
  bottom: 0;

}

.total-box p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #ABBBC2;
  margin-top: 17px;
}

.total-box h2 {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  text-align: right;
  color: #FFFFFF;
  margin-top: 17px;
}

.payment-btn {
  width: 100%;
  margin-top: 42px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 14px;
  background: #EA7C69;
  box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
  border-radius: 8px;
  outline: none;
  border: none;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  color: #FAFAFA;
}
.payment-btn:hover{
  border: 1px solid #EA7C69;
  color: #EA7C69;
  background: transparent;
  box-shadow: none;
}

.zakaz{
  height: 62vh;
  overflow: scroll;
  
}
.zakaz::-webkit-scrollbar{
  display: none;
}