.choose{
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: #FFFFFF;
  }
  
  .dishes-holder{
      width: 100%;
      height: 650px;
      overflow-y: scroll;
      padding-bottom: 20px!important;
      box-sizing: border-box;
  }
  
  .dishes-holder::-webkit-scrollbar {
      width: 3px;
    }
     
    .dishes-holder::-webkit-scrollbar-track {
      
      background-color: #1F1D2B;
    }
     
    .dishes-holder::-webkit-scrollbar-thumb {
      background-color: #EA7C69;
      box-shadow: inset 0 0 6px rgba(234, 124, 105 , 0.5);
    }
  
  .first{
      margin-top: 24px;
      align-items: center;
      margin-bottom: 50px;
  }
  
  .foodCard {
      width: 220px;
      height: 235px;
      background: #1F1D2B;
      border-radius: 16px;
      text-align: center;
      margin:0 30px 58px;
  }
  
  .food-img {
      width: 132px;
      height: 132px;
      border-radius: 50%;
      margin-top: -36px;
       animation: rotation 15s infinite linear; 
       animation-delay: 0.8s;
      object-fit: cover;
  }
  
  @keyframes rotation{
      from{
          transform: rotate(0deg);
      }
      to{
          transform: rotate(360deg);
      }
  }
  
  .food-title {
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      text-align: center;
      color: #FFFFFF;
      padding: 0px 24px;
      margin-top: 20px;
  }
  
  .food-price {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 140%;
      text-align: center;
      color: #FFFFFF;
      margin: 8px 77px 4px 77px;
  }
  
  .available {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 140%;
      text-align: center;
      color: #ABBBC2;
      padding: 4px 40px 24px 40px;
  }
  
  .row-wrap{
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 80px;
  }